<template>
<div class="fx-healthy">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-content">
        <div class="fx-space"></div>
        <!-- 投被 保险人都有 -->
        <div class="fx-tab bg-style1" v-if="healthyType === '0' &&!showInsureHealthContent">
            <div class="text text1">投保人健康告知</div>
            <div class="text">被保险人健康告知</div>
        </div>
        <div class="fx-tab bg-style2" v-if="healthyType === '0' && showInsureHealthContent">
            <div class="text text1">投保人健康告知</div>
            <div class="text">被保险人健康告知</div>
        </div>
        <!-- 只有被保险人 -->
        <div class="fx-tab bg-style3" v-if="healthyType === '1'">
            被保险人健康告知
        </div>
        <div v-if="!showInsureHealthContent" v-html="holderHealthContent" class="wrapper" :style="{overflow:'auto',height:contentHeight+'px'}"></div>
        <div v-if="showInsureHealthContent" v-html="insureHealthContent" class="wrapper" :style="{overflow:'auto',height:contentHeight+'px'}"></div>
        <div v-if="healthyType === '1'" class="wrapper" v-html="insureHealthContent" :style="{overflow:'auto',height:contentHeight+'px'}"></div>
    </div>
    <div class="fx-btns" v-if="!showInsureHealthContent">
        <div class="preBtn" @click="showModal(1)"><span class="sp1">部分情况有</span><span class="sp2">(支持智能审核)</span></div>
        <div class="nextBtn" @click="goNext()">以上情况全无</div>
    </div>
    <div class="fx-btns" v-if="showInsureHealthContent">
        <div class="preBtn" @click="showModal(2)"><span class="sp1">部分情况有</span><span class="sp2">(支持智能审核)</span></div>
        <div class="nextBtn" @click="goNext()">以上情况全无</div>
    </div>
    <pop-modal :modalHeight=236 :modalTitle="modalTitle" :modalContent="modalContent" @cancel-event="hideModal()" @sure-event="goIntellCheck()"></pop-modal>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import popModal from "@/components/modal";
export default {
    name:'healthy',
    data(){
        return{
            type:null,
            orderId:null,
            healthyType:null,//0有投保人健康告知 1无投保人健康告知
            showInsureHealthContent:false,
            contentHeight:0,
            headerTitle:'健康告知',
            holderHealthContent:"",//投保人健康告知
            insureHealthContent:"",//被保险人健康告知
            modalTitle:"您即将进入智能核保",
            modalContent:"如被保险人健康告知上有相关问题， 可通过智能核保了解被保险人健康状 况，快速得到核保结果。",
        }
    },
    created() {

    },
    methods: {
        //showModal
        showModal(type){
            //console.log(type);
            this.type = type;
            //healthyType:null,//0有投保人健康告知 1无投保人健康告知
            let healthyType = this.healthyType;
            if(healthyType === '1' || type === 1){
                this.modalTitle = "温馨提示"
                this.modalContent = "投保人健康告知不符合保险公司要求， 将无法投保该产品！"
            }
            else{
                this.modalTitle = "您即将进入智能核保"
                this.modalContent = "如被保险人健康告知上有相关问题， 可通过智能核保了解被保险人健康状 况，快速得到核保结果。"
            }
            document.getElementsByClassName("fx-modal")[0].style.display="block";
        },
        //hideModal
        hideModal(){
            document.getElementsByClassName("fx-modal")[0].style.display="none";
        },
        //进入智能审核
        goIntellCheck(){
            let healthyType = this.healthyType
            //console.log(this.type);
            if(healthyType === '1' || this.type === 1){
                this.hideModal();
            }
            else{
                //this.type
                sessionStorage.setItem("TYPE",this.type);
                this.$router.push({path:'/fuxing/index'})
            }
        },
        //进入下一步
        goNext(){
            //做一个判断，如果没有投保人健康告知
            //healthyType:null,//0有投保人健康告知 1无投保人健康告知
            let healthyType = this.healthyType,showInsureHealthContent = this.showInsureHealthContent;
            if(healthyType === '0' && !showInsureHealthContent){
                this.showInsureHealthContent = true;
            }
            else if(healthyType === '0' && showInsureHealthContent){
                this.goNext1();
            }
            else if(healthyType === '1'){
                this.goNext1();
            }
        },
        goNext1(){
            //跳到订单页面
            console.log("跳到订单页面");
            let JUMP_KEY = sessionStorage.getItem("JUMP_KEY");
            let ORDER_ID = sessionStorage.getItem("ORDER_ID");
            window.location.href = this.$common.api_root + "/jump.html?url="
                + encodeURIComponent("./market/writeInform-3.html?key=" + JUMP_KEY+"&proCode=" + ORDER_ID + "")
        },
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //请求健康告知
        getHealthContent(){
            let THAT = this;
            THAT.$http.getHttp({
                name:'fxIllList',
                params:{},
                orderId:THAT.orderId,
                type:'get'
            },
            res => {
                THAT.holderHealthContent = res.data.holderHealthContent
                THAT.insureHealthContent = res.data.insureHealthContent
            })
        }
    },
    computed:{},
    components:{
        "header-text":headerText,
        "pop-modal":popModal,
    },
    mounted(){
        let contentHeight = window.innerHeight - document.getElementsByClassName("fx-btns")[0].clientHeight;
        this.contentHeight = contentHeight;
        console.log(window.innerHeight);
        //根据type判断
        console.log(this.$route.query.healthyType);
        let healthyType = this.$route.query.healthyType,orderId = this.$route.query.policyId,jumpKey = this.$route.query.key;
        this.healthyType = healthyType;
        this.orderId = orderId;
        sessionStorage.setItem("HEALTHY_TYPE",healthyType);
        sessionStorage.setItem("ORDER_ID",orderId);
        sessionStorage.setItem("JUMP_KEY",jumpKey);
        this.getHealthContent();
    }
}
</script>

<style lang="scss">
.fx-healthy{
    width:100%;
    height:100%;
    background:#fff;
    position: relative;
    overflow: hidden;
    .fx-content{
        overflow: hidden;
        .wrapper{
            padding-bottom:r(150);
        }
        .fx-space{
            height:r(16);
            width:100%;
        }
        .bg-style1{
            height:r(134);
            width:r(750);
            padding-top:r(75);
            background: url("../../../static/images/tab1.png") no-repeat;
            background-size:100% r(75);
        }
        .bg-style2{
            height:r(134);
            width:r(750);
            padding-top:r(75);
            background: url("../../../static/images/tab2.png") no-repeat;
            background-size:100% r(75);
        }
        .bg-style3{
            height:r(60);
            width:r(750);
            text-align:center;
            line-height:r(60);
            font-size: r(30);
            border-bottom:r(2) solid #ddd;
        }
        .fx-tab{
            
            .text{
                font-size: r(28);
                width: 50%;
                float: left;
                text-align: center;
                height:r(59);
                line-height:r(59);
                color:#000;
            }
            .text1{
                color:#0084ff;
            }
        }
    }
    .fx-btns{
        height:r(92);
        position:absolute;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:50%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .preBtn{
            color:#000;
            background:#fff;
            .sp1{
                display:block;
                line-height:r(56);
            }
            .sp2{
                font-size:r(22);
                display: block;
            }
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
        .nextBtn1{
            width:100%;
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
    }
}
</style>


